
*{
	margin: 0;
	padding: 0;
	box-sizing:border-box;
	-moz-box-sizing:border-box; 
	-webkit-box-sizing:border-box;
}
#app {
 width: 100%;
}
.has-header {
  margin-top: 0.48rem;
}
ul, li, a, h2 {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
.header-bar {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 998;
  height: 0.9rem;
  padding: 0 0.36rem;
  background: #fff;
  border-bottom: 2px solid #f3f3f3;
}



